@tailwind base;
@tailwind components;
@tailwind utilities;

:root{
  --bgMain: rgb(23, 99, 134);
  --bgHF: rgb(41, 131, 173);
}

html, body{
  height:100%;
}
body{
  min-height: 100%;
}

#root{
  background: var(--bgMain);
}

.header{
  background: var(--bgHF);
}

.footer {
  background: var(--bgHF);
  display: flex;
  justify-content: center;
  padding: 1rem;
}

.footer img{
  width:50px;
  height:50px;
}